@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@100;200;300;400;500;600;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap);
#Home {
  display: grid;
  width: 1150px;
  margin: 0 auto;
  grid-template-columns: 1fr 570px;
  grid-gap: 100px;
  gap: 100px;
}

#Home main #About {
  display: none;
}

/* For smaller devices */
@media (max-width: 1150px) {
  #Home {
    display: block;
    width: 100%;
    padding: 0 30px;
  }

  #Home main #About {
    display: block;
  }
}

header#Head {
  align-items: flex-start;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

#Head .logo {
  max-height: 60px;
  margin-bottom: 20px;
}

#Head .foot {
  display: flex;
  margin-top: 20px;
}

/* For smaller devices */
@media (max-width: 1150px) {
  #Head {
    position: static;
    justify-content: flex-end;
    height: 110px;
  }

  #Head .logo {
    max-height: 50px;
    margin: 0 auto;
  }

  #Head .content {
    display: none;
  }
}

#About .social {
  display: none;
  margin-top: 15px;
}

/* For smaller devices */
@media (max-width: 1150px) {
  #About {
    padding: 30px 0;
  }

  #About .social {
    display: block;
  }
}

#SocialButtons {
  display: flex;
}

#SocialButtons a {
  position: relative;
  width: 30px;
  height: 30px;
  margin-right: 5px;
  border-radius: 3px;
  text-align: center;
}

#SocialButtons a:hover {
  opacity: 0.6;
}

#SocialButtons img {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

#Menu nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

#Menu li a {
  display: block;
  font: 400 12px 'Inter', sans-serif;
  color: var(--secondary-color);
  letter-spacing: 3px;
  margin-top: 15px;
  text-transform: uppercase;
  text-decoration: none;
  cursor: pointer;
  transition: 300ms;
}

#Menu li a hr {
  display: inline-block;
  width: 30px;
  border: 0;
  height: 0;
  border-top: 1px solid var(--secondary-color);
  margin-right: 10px;
  vertical-align: middle;
  transition: 300ms;
}

#Menu nav ul li:hover a,
#Menu nav ul li .active {
  color: var(--primary-color);
}

#Menu nav ul li:hover hr,
#Menu nav ul li .active hr {
  border-color: var(--primary-color);
  width: 60px;
}

/* For smaller devices */
@media (max-width: 1150px) {
  /* #Menu nav ul li a {
    display: none;
  }
  #Menu nav ul .active {
    display: block;
  } */
}

#LangSwitcher button {
  width: 70px;
  height: 31px;
  padding: 0;
}

#Hero .content {
  text-align: center;
}

#Hero .memoji {
  max-height: 300px;
}

#Hero .arrow-down {
  max-height: 15px;
  cursor: pointer;
  -webkit-animation: floating 1s infinite alternate;
  animation: floating 1s infinite alternate;
}

#Hero .to-about {
  display: none;
}

/* For smaller devices */
@media (max-width: 1150px) {
  #Hero {
    height: 90vh;
    margin-top: -110px;
  }

  #Hero .to-resume {
    display: none;
  }

  #Hero .to-about {
    display: block;
  }
}

/* For landscape and tablet devices */
@media (min-width: 480px) and (max-width: 1150px) {
  #Hero {
    height: auto;
    margin-top: 0px;
  }
}

@-webkit-keyframes floating {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  100% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
}

@keyframes floating {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  100% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
}

